import React, { Component } from 'react';
import axios from 'axios';

class Statistics
 extends Component {
  state ={    
    experience:0,
    clients:0,
    projects:0,
    branches:0
  }
  
  componentDidMount()
{
  axios.get(`http://erp.ctrlpw.com.sa/api/resource/Statistics?fields=["*"]&limit=1`
)      
    .then((res)=>{                
         console.log(res.data.data)    
         this.setState({
       
          experience : res.data.data[0]['experience'],
          clients : res.data.data[0]['clients'],
          projects : res.data.data[0]['projects'],
          branches : res.data.data[0]['branches']
         })
    }
  )
  
} 

 
    render() { 
        return ( 
             <div className="section counts bg-primary text-center">
        <div className="container">
          <div className="card border-0 bg-transparent text-muted">
            <div className="card-body">
              <div className="row row-cols-lg-4 row-cols-md-2 row-cols-sm-2 row-cols-1 g-1 text-center">
                <div className="col">
                  <div className="card border-0 bg-transparent">
                    <div className="card-body">
                      <h2 className="display-4 fw-bold chart-text-color3">
                      {this.state.experience}
                        +</h2>
                      <h6 className="mb-0 text-uppercase">خبرتنا في مجال السوق</h6>
                    </div>
                  </div>
                </div>
                <div className="col">
                  <div className="card border-0 bg-transparent">
                    <div className="card-body">
                      <h2 className="display-4 fw-bold chart-text-color3">
                      {this.state.clients}+</h2>
                      <h6 className="mb-0 text-uppercase">عدد عملائنا</h6>
                    </div>
                  </div>
                </div>
                <div className="col">
                  <div className="card border-0 bg-transparent">
                    <div className="card-body">
                      <h2 className="display-4 fw-bold chart-text-color3"> {this.state.projects}+</h2>
                      <h6 className="mb-0 text-uppercase">عدد مشاريعنا </h6>
                    </div>
                  </div>
                </div>
                <div className="col">
                  <div className="card border-0 bg-transparent">
                    <div className="card-body">
                      <h2 className="display-4 fw-bold chart-text-color3">{this.state.branches}+</h2>
                      <h6 className="mb-0 text-uppercase">عدد فروعنا</h6>
                    </div>
                  </div>
                </div>
              </div>  
            </div>
          </div>
        </div>
      </div>
       );
    }
}
 
export default Statistics
;