import React, { Component } from 'react';
import axios from 'axios';


class Sectors extends Component {
  state ={ 
    dataList :[]
  }
  
componentDidMount()
{
  axios.get(`http://erp.ctrlpw.com.sa/api/resource/sectors?fields=["*"]`
)      
    .then((res)=>{                
         console.log(res.data.data)    
         this.setState({
          dataList : res.data.data
         })
    }
  )
} 

  
    render() { 
        return (
            <div className="section why-us pt-0" id="why-us">
            <div className="container">
              <div className="row g-3 row-deck text-center">
                <div className="col-12">
                  <div className="card bg-secondary text-white py-4">
                    <div className="card-body">
                      <h2 className="fw-bold">استثماراتنا الحالية
                      </h2>
                      <p className="lead">
                        نبحث عن شركاء يسعون
                         بجهودهم لتحقيق التميّز والتطوّر،
        
                      </p>
                      <p className="px-xl-5 px-lg-4 m-auto lead mb-5">
                        ولهذا 
                        السبب في الأساس تقود شركاتنا  
                         اباستمرار عبر مختلف الصناعات والمجالات.
                      </p>
                    </div>
                  </div>
                </div>
                {this.state.dataList.map(
                item=>
                <div key={item.name} className="col-xl-4 col-lg-6">
                <div className="card">
                  <div className="card-body">
                 
                    <h5> 
                      {item.title}
                    </h5>
                  </div>
                </div>
              </div>           
          )}

               
           
               
              </div>  
            </div>
          </div>
          );
    }
}
 
export default Sectors;