import React, { Component } from 'react';
import axios from 'axios';
import parse from 'html-react-parser'


class ChairmanWord extends Component {
  
    state ={
 
      fullname:null,
      content:'',
      img:null
    }
    
componentDidMount()
{
    axios.get(`http://erp.ctrlpw.com.sa/api/resource/ChairmanWord?fields=["*"]&limit=1`
  )      
      .then((res)=>{                
           console.log(res.data.data)    
           this.setState({
         
            fullname : res.data.data[0]['fullname'],
            content : res.data.data[0]['content'],
            img : res.data.data[0]['img']
           })
      }
    )
    
} 
    render() { 

        return (    
          <div className="section about" id="about">
          <div className="container">
            <div className="row g-3">
              <div className="col-xl-5 col-lg-6">
                   <img src={'http://erp.ctrlpw.com.sa' + this.state.img}
                  width="100%"
                  className=" d-flex justify-content-center align-items-stretch position-relative rounded-4  me-0" />
               </div>
              <div className="col-xl-7 col-lg-6">
                <p>
                كلمة رئيس مجلس الإدارة 
                </p>
                <p className="mb-1 mt-4 color-900">
                  <strong>
                  {this.state.fullname}
                  </strong> 
                </p>
                <hr/>
               

                {parse(this.state.content)}                         
               
              </div>
            </div>  
          </div>
        </div>
        
      );
    }
}
 
export default ChairmanWord;












 