import React, { Component } from 'react';
import axios from 'axios';
import Moment from 'moment';
import {
  BrowserRouter  ,
  Route ,Routes,Link
} from "react-router-dom";

class News extends Component {
    state ={ 
        dataList :[]
      }
      
    componentDidMount()
    {
      axios.get(`http://erp.ctrlpw.com.sa/api/resource/news?fields=["*"]`
    )      
        .then((res)=>{                
             console.log(res.data.data)    
             this.setState({
              dataList : res.data.data
             })
        }
      )
    } 
    render() { 
        return ( 
            <div class="blog-posts py-5">
            <div class="container">
              <div class="row g-4">
                <div class="col-xl-12 col-lg-12 col-md-12">
                  <div class="text-uppercase mb-3 pb-2 border-bottom">
                    <h2 className='mb-1 mt-4 color-900'>
                    اخر الاخبار
                    </h2>
                    </div>
                    <div className='row'>                 
                    {this.state.dataList.map(
                item=>
               <div className='col-md-6 '> 
               <div className='card m-2 pb-4'>
               
                <div  key={item.name} class="card bg-transparent border-0">
                <div class="row g-0 align-items-center">
                  <div class="col-xl-4 col-lg-4 col-md-4 m-4">
                    <img  src={'http://erp.ctrlpw.com.sa'+ item.img}  class="img-fluid rounded" alt="..."  />
                  </div>
                  <div class="col-xl-7 col-lg-8 col-md-8">
                    <div class="ps-0 ps-md-4 pt-2 pt-md-0">
                
                      <Link to={`/Details/${item.name}`} class="h4 d-flex mb-md-3 mb-2 color-600">
                        <p>{item.newstitle}  </p>                      
                        </Link>
                      <div class="small text-muted text-uppercase">
                        <span class="post-on">
                        {Moment(item.creation).format('llll')} 
                             </span>
                   
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              </div>
              </div>
          )}
    </div>

                
            
            
             
                </div>
            
            
              </div>  
            </div>
          </div>
         );
    }
}
 
export default News;