import React, { Component } from 'react';
import axios from 'axios';

class OurGroup extends Component {
  state ={ 
    dataList :[]
  }
  
componentDidMount()
{
  axios.get(`http://erp.ctrlpw.com.sa/api/resource/OurGroup?fields=["*"]`
)      
    .then((res)=>{                
         console.log(res.data.data)    
         this.setState({
          dataList : res.data.data
         })
    }
  )
} 
    render() { 
        return (   <div className="section services-2" id="services">
        <div className="container">
          <div className="row justify-content-center mb-3 text-center">
            <div className="col-lg-12">
               <h2 className="mb-1 mt-4 color-900">مجموعتنا </h2>
             </div>
          </div> 
          <div className="row row-cols-lg-3 row-cols-md-2 row-cols-sm-2 row-cols-1 g-0 row-deck">
          {this.state.dataList.map(
                item=>
                <div key={item.name} className="col p-2">
                <div className="card p-lg-5 p-4 text-center">
                 <center>
                    <img src={'http://erp.ctrlpw.com.sa'+ item.logofile} height="100" width="100" />
                 </center>
                  
                  <h4>
                    <a href="#" className="color-600">                                           
                  {item.namecom}
                  </a></h4>             
                </div>
              </div>
          )}
 
          </div>  
        </div>
      </div> );
    }
}
 
export default OurGroup;