import React, { Component } from 'react';
import axios from 'axios';

class Hero extends Component {
 
  state ={
 
      dataList :[]
    }
    
componentDidMount()
{
    axios.get(`http://erp.ctrlpw.com.sa/api/resource/filePDF?fields=["*"]`
  )      
      .then((res)=>{                
           console.log(res.data.data)    
           this.setState({
            dataList : res.data.data
           })
      }
    )
} 

    render() { 
         return (             
            <div className="section hero d-flex align-items-center" id="hero">
        <div className="container">
          <div className="row g-3 py-5 align-items-center justify-content-between">
            <div className="col-xxl-5 col-lg-6 col-md-12">
              <h5 className="color-900">
                اقـــــتــــــــــرب
              </h5>
              <h2 className="bg-text color-900">
                <span className="text-gradient fw-bold">
                 أكثر من دروازة       
                    </span> 
    
              </h2>
              <p className="color-500  mb-4">
                هل انت مستعد لمستقبل مختلف تماماً
                نؤمن أن لدينا 
                <br/>
                مسؤولية تجاه المجتمع تتمثل في نشر الوعي من أجل رد الجَميل للعالم.
                
              </p>
              {this.state.dataList.map(
                item=>
              <a key={item.name} href={'http://erp.ctrlpw.com.sa' + item.filepdf} 
              target="self"
              className="btn btn-lg px-5 bg-secondary text-white text-uppercase fs-6">                                        
                   Profile Company           
                     &nbsp;
                  <i className="fa fa-file-pdf-o "></i>
              </a>
          )}
 
              <ul className="social mb-0 list-inline mt-5">
                <li className="list-inline-item"><a href="/" className="pe-3 color-900 fs-6">المنهج </a></li>
                <li className="list-inline-item"><a href="/Curriculum" className="pe-3 color-900 fs-6">القيم  </a></li>
                <li className="list-inline-item"><a href="#" className="pe-3 color-900 fs-6">مجلس الادارة </a></li>
    
              </ul>
            </div>
            <div className="col-xxl-6 col-lg-6 col-md-12">
            {this.state.dataList.map(
                item=>
          <img key={item.name} className="img-fluid d-none d-lg-block" width="250" height="500"  src={'http://erp.ctrlpw.com.sa' + item.imgpage} />
          )}
              
            </div>
          </div> 
        </div>
      </div>  
      );
    }
}
 
export default Hero;